/**
 *Genel Anket kontratdr.
 */
import {getDateIsoDate} from "../Types/Common";

export class VoteContract {
    voteId;
    voteCode;
    name;
    apartmentId;
    userChoice;
    description;
    isActive;
    startDate;
    endDate;
    /**
     * oy pusulas
     */
    optionList;
    userChoiceList;
    constructor() {
        this.userChoice = new VoteItem();
        this.startDate = getDateIsoDate();
        this.endDate = getDateIsoDate();
        this.name = '';
        this.apartmentId = 0;
        this.optionList = [];
        this.userChoiceList = [];
    }
}

/**
 * kullanc seimidir. 
 */
export class VoteItem{
    voteOption;
    userHash;
    userId;
    constructor() {
        this.voteOption = new VoteOption();
        this.userHash = '';
    }
}
/**
 * seim pusulasndaki her bir seenektir
 */
export class VoteOption{
    code;
    description;
}

/**
 * seim sonu kontrat
 */
export class VoteResultContract{
    voteId;
    name;
    apartmentId;
    optionCode;
    optionDescription;
    voteCount;
}